@import "mixin";

body {

}

.navbar {
  a {
    text-decoration: none;
    color: rgba(255, 255, 255, .7);
    border-bottom: 1px solid transparent;
    @include transition(.5s);
    &:hover {
      background: transparent !important;
      color: #FFFFFF;
      border-bottom-color: #FFFFFF;
    }
  }
}

.my-nav {
  box-shadow: 0 -165px 88px 0px black inset;
  overflow: hidden;
  background-image: url("../image/background.jpg");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  -webkit-clip-path: polygon(100% 0, 100% 64%, 50% 90%, 0 64%, 0 0);
  clip-path: polygon(100% 0, 100% 64%, 50% 90%, 0 64%, 0 0);
  .my-content {
    a {
      text-decoration: none;
      color: inherit;
    }

    width: 100%;
    height: 100vh;
    background: rgba($mainColor, .8);
    color: #FFFFFF;

    .content-header {
      .center-image {
        display: block;
        margin: 20px 35%;
      }
      .h3 {
        margin: 0;
        &.first {
          margin: 30px 0 10px;
        }
      }
      @media (min-width: 768px) {
        padding: 100px 0 90px;

      }
      text-align: center;
      .sec-div {
        padding-top: 30px;
        .titles-parent {
          img {
            width: 50%;
          }
          width: 100%;
          text-align: center;
          .my-supTitle {
            font-size: 30px;
            position: absolute;
            top: 30px;
          }
        }
      }
      @media (max-width: 767px) {
        padding: 50px 0;
        .sec-div {
          padding-top: 20px;
        }
      }

      p {
        width: 100%;
      }
      .footer-btn-header {
        margin: 40px auto 0;
        .btn-left {
          height: 50px;
          width: 150px;
          margin: 3px;
          display: inline-block;
          font-family: fontBlack !important;
          line-height: 50px;
          border: solid 1px #FFFFFF;
          @include borderR(7px);
          &:hover {
            color: $mainColor;
            border-color: $mainColor;
            background: #FFFFFF;
          }
        }
      }
    }
  }
}

@include keyframes(bounce) {
  0%, 20%, 50%, 80%, 100% {
    @include transform(translateY(0));
  }
  40% {
    @include transform(translateY(-20px));
  }
  60% {
    @include transform(translateY(-10px));
  }
}

.scrollin-btn {
  display: block;
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  @include borderR(50%);
  @include transition(1s);
  cursor: pointer;
  border: 1px solid;
  animation: bounce 2s infinite;
  &#go-to-down {
    position: absolute;
    bottom: 90px;
    left: calc(50% - 25px);
    background: rgba(255, 255, 255, .1);
    //color: #FFFFFF;
    border-color: #fff;
    &:hover {
      background: #FFFFFF;
      color: $mainColor;
    }
  }
  &#go-to-up {
    display: none;
    position: fixed;
    bottom: 50px;
    right: 50px;
    background: rgba($mainColor, .1);
    color: $mainColor;
    border-color: $mainColor;
    &:hover {
      background: $mainColor;
      color: #FFFFFF;
    }
  }
}

.section {
  overflow: hidden;
  padding: 0 !important;
  &.sec-section {
    background: $backgroundColor;
    .col-md-6 {
      float: left;
    }
  }
  .col-md-6 {
    padding: 0 !important;
    margin: 0 !important;
    video {
      vertical-align: middle;
    }
  }

  h1 {
    color: $mainColor;
    padding: 15px;
    text-align: center;
    border-radius: 50px 0 0 50px;
    font-size: 23px;
    @media (max-width: 767px) {
      font-size: 17px;
    }
  }
  p {
    padding: 20px;
    font-size: 16px;
  }
}

.gallery {
  padding: 15px 0;
  .flickity-prev-next-button {
    background: rgba($mainColor, .4);
    .flickity-button-icon {
      fill: $mainColor;
    }
    &:disabled {
      display: none;
    }
  }
  .gallery-cell {
    width: 33.3333%;
    height: 200px;
    margin-right: 10px;
    filter: grayscale(1);
    background-size: cover;
    background-position: center;
    a {
      height: 40px;
      width: 60px;
      line-height: 40px;
      @include borderR(7px);
      margin: 80px auto 0;
      text-align: center;
      background: rgba(#fff, .7);
      color: $mainColor;
      border: 1px solid $mainColor;
      @include transition(1s);
      display: none;
    }
    p {
      background-color: rgba($mainColor, .7);
      color: #FFFFFF;
      position: absolute;
      bottom: 0;
      margin: 0;
      padding: 5px;
      font-size: 13px;
    }
    &.is-selected {
      filter: grayscale(0);
      a {
        display: block;
        &:hover {
          background: $mainColor;
          color: #FFFFFF;
        }
      }
      p {
        font-size: 15px;
      }
    }
  }
}

